.நடுநிலை {
    text-align: center;
}

.தலையங்கம் {
    background: #5d9cfd;
    color: white;
    font-family: 'Arima Madurai', cursive;
    width: 90vw;
    height: 5vh;
    padding-bottom: 0.5vh;
    padding-left: 5vw;
    padding-right: 5vw;
    position: fixed;
    z-index: 10;
}

.தலைப்பு {
    font-size: 4em;
    -webkit-tap-highlight-color: transparent;
}

.பட்டியல் {
    font-size: 2.2em;
    top: -5.9vh;
    position: relative;
    text-align: right;
    font-weight: bold;
    margin-left: 50vw;
}

.பட்டியல் > span {
    -webkit-tap-highlight-color: transparent;
}

.பட்டியல் > i.சூழற்கொத்து {
    position: relative;
    top: 15px;
    font-size: 1.7em;
    -webkit-tap-highlight-color: transparent;
}

.தளம் {
    padding-top: 5.5vh;
}